.Wallet-box-wrap {
  height: fit-content;
  width: fit-content;
  padding: 10px;
  background: #5a225b;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25),
    inset 4px 4px 25px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wallet-title {
  font-family: poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  color: #fdad00;
}
